@import "../base/global";

.container {
	inline-size: var(--container-width, 80%);
	min-inline-size: var(--container-min-width, #{$width-laptop-s});
	padding-inline: var(--container-pad-x, #{$gap-xxl});
	margin-inline: auto;

	@include res-max-laptop-l {
		--container-width: 75%;
		--container-min-width: 0;
	}

	@include res-max-laptop-m {
		--container-width: 85%;
	}

	@include res-max-laptop-s {
		--container-width: 90%;
	}

	@include res-max-tablet {
		--container-width: 100%;
	}

	@include res-max-mobile-l {
		--container-pad-x: #{$gap-xl};
	}

	@include res-max-mobile-m {
		--container-pad-x: #{$gap-l};
	}

	&--s {
		--container-width: max(#{$width-laptop-s}, calc(#{$width-mobile-s} - #{$gap-xl}));
		--container-min-width: 0;
	}
}
