* {
    box-sizing: border-box;
}

html {
    height: 100%;
    margin: 0;
    font-size: 62.5%;
    scroll-behavior: smooth;
}

body {
    position: relative;
    padding: 0; 
    margin: 0;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

address{
    font-style: normal;
}
